body {
    background-image: linear-gradient(to right, #2D3741, #0A091F);
    /* overflow-y: hidden; */
}

.home {
    position: relative;
    display: grid;
    grid-template-columns: 16rem auto 16rem;
    gap: 1rem;
}

@media only screen and (max-width: 1024px) {
    .home {
        display: grid;
        grid-template-columns: auto 16rem;
    }
  }

@media only screen and (max-width: 768px) {
    .home {
        display: block;
    }
}
